let createTree = (value)=>{
  return {
    data:value,
    children: null,
    parent: null,
  }
};
let appendChild = (node,value) =>{
  const newNode = {
    data: value,
    children: null,
    parent: node,
  };
  //nodeå°±è¡¨ç¤ºtreeè¿™ä¸ªèŠ‚ç‚¹
  node.children = node.children || [];//å‡å¦‚treeæœ‰è‡ªå·±çš„å­©å­ï¼Œå°±è®©tree.next(ä¹Ÿå°±æ˜¯tree.children)=tree.children;å¦‚æœæ²¡æœ‰å­©å­ï¼Œé‚£å°±ç”¨"[]"è¡¨ç¤º
  node.children.push(newNode);//å°†è‡ªå·±çš„å­©å­æ·»åŠ åˆ°å­˜æ”¾å­©å­çš„æ•°ç»„ä¸­
  return newNode;
};
const travel = (tree,fn)=>{
  fn(tree);//è°ƒç”¨tarvelè¿™ä¸ªå‡½æ•°
  if(!tree.children){return}//å¦‚æœæ²¡æœ‰å­©å­å°±ç›´æ¥é€€å‡º
  for(let i=0;i<tree.children.length;i++){//éå†tree.childrenè¿™ä¸ªæ•°ç»„
    travel(tree.children[i],fn);//é€šè¿‡éå†ï¼Œæ‹¿åˆ°æ¯ä¸€ä¸ªchildrenå¹¶ä¼ ç»™ä½ fn,
  }
};
//ä»treeä¸­åˆ é™¤ä¸€ä¸ªèŠ‚ç‚¹
const removeChildren = (tree,node)=>{
  const siblings = node.parent.children;//æ‰¾åˆ°ä»–çš„å…„å¼Ÿå§å¦¹
  //å¯»æ‰¾éœ€è¦åˆ é™¤çš„nodeçš„ä¸‹æ ‡
  let index = 0;//è¡¨ç¤ºè‚¯å®šå­˜åœ¨è¿™ä¸ªä¸‹æ ‡
  for(let i = 1;i<siblings.length;i++){//éå†å…„å¼Ÿå§å¦¹ä»¬
    if(siblings[i] === node){//å¦‚æœå…¶ä¸­æœ‰ä¸€ä¸ªç­‰äºè¿™ä¸ªèŠ‚ç‚¹
      i = index;//è®©æ‰¾åˆ°çš„è¿™ä¸ªnodeçš„ä¸‹æ ‡ç­‰äºåˆšåˆšåˆå§‹åŒ–çš„ä¸‹æ ‡
    }
  }
  siblings.splice(index,1);//ä»å…„å¼Ÿå§å¦¹ä¸­åˆ é™¤å®ƒ
};
//å®šä¹‰findæ–¹æ³•
const find = (tree,node) =>{//åœ¨treeä¸­æ‰¾nodeè¿™ä¸ªèŠ‚ç‚¹
  if(tree === node){
    return tree;
    //å¦‚æœåœ¨treeä¸­æ²¡æœ‰æ‰¾åˆ°èŠ‚ç‚¹node,å°±ç»§ç»­åˆ°å®ƒçš„å„¿å­ä¸­æ‰¾
  }else if(tree.children){
    for(let i=0;i<tree.children.length;i++){//éå†æ‰€æœ‰å„¿å­
     const result =  find(tree.children[i],node);//å¦‚æœç¬¬iä¸ªå„¿å­ = node,å°±è®°ä¸ºresult
     if(result){return result;}//å¦‚æœresultå­˜åœ¨ï¼Œå°±è¿”å›result
    }
    return undefined;//å¦‚æœå„¿å­ä¸­ä¹Ÿæ²¡æœ‰æ‰¾åˆ°ï¼Œå°±è¿”å›undefined
  }else{
    return undefined;//æ‰€æœ‰èŠ‚ç‚¹ä¸­éƒ½æ‰¾ä¸åˆ°è¿™ä¸€ä¸ªå°±è¿”å›undefined
  }
};
const tree = createTree(10);
const node2 =appendChild(tree,20);
